import { faCaretLeft, faCaretRight } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from "gatsby";
import React from "react";
import "./nav-pagination.scss";
export const NavPagination = ({ options }) => {
  const { previousPageUrl, last, index, first, pathPrefix, nextPageUrl, nums } =
    options;
  return (
    <div className="container">
      <div className="paginate-nav">
        {!first && (
          <Link className="paginate-nav__btn" to={previousPageUrl}>
            <FontAwesomeIcon icon={faCaretLeft} />
          </Link>
        )}
        {nums.map((item) => {
          return (
            <Link
              className={`${item === index ? "active" : ""} paginate-nav__btn`}
              to={`${item === 1 ? pathPrefix : `${pathPrefix}/${item}`}`}
            >
              {item}
            </Link>
          );
        })}
        {!last && (
          <Link className="paginate-nav__btn" to={nextPageUrl}>
            <FontAwesomeIcon icon={faCaretRight} />
          </Link>
        )}
      </div>
    </div>
  );
};
