import React from "react";
import "./actualites-page-style.scss";
import { graphql, useStaticQuery } from "gatsby";

import HeroSection from "../../molecules/HeroSection/HeroSection";
import { BoxDonation } from "../../molecules/BoxDonation/BoxDonation";
import { AboutSection } from "../../organisms/AboutSection/AboutSection";
import ActionBox from "../../molecules/ActionsBox/action-box";
import { NavPagination } from "../../molecules/pagination-nav/NavPagination";
import WrapperContainer from "../container/WrapperContainer";
const ActualitesPageWrapper = ({ content }) => {
  const { desktop } = useStaticQuery(graphql`
    query ActusHero {
      desktop: file(relativePath: { eq: "nos-actualites/hero-image.jpg" }) {
        childImageSharp {
          fluid(quality: 100, maxWidth: 1920) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);
  const { group, index, heroContent, last, first, pageCount, pathPrefix } = content;
  const previousIndex = index - 1;
  const nextIndex = index + 1;
  const previousPageUrl = previousIndex === 1 ? pathPrefix : `${pathPrefix}/${previousIndex}`;
  const nextPageUrl = `${pathPrefix}/${nextIndex}`;
  const nums = Array.from({ length: pageCount }).map((_, i) => i + 1);
  const options = {
    previousPageUrl,
    last,
    first,
    pathPrefix,
    index,
    nextPageUrl,
    nums,
  };
  return (
    <WrapperContainer>
      <HeroSection image={desktop.childImageSharp.fluid}>
        <div className="heroInfo d-center">
          <h1 className="heading-standard hero-title-center text-white">Actualités de l'association</h1>
        </div>
      </HeroSection>
      <AboutSection descriptions={heroContent.description} />
      <section className="mission-section">
        <div className="container">
          <div className="mission-section__content">
            {group.map((item, index) => {
              return <ActionBox {...item} key={index} href={`${pathPrefix}/actualites/`} />;
            })}
          </div>
        </div>
        <NavPagination options={options} />
      </section>
    </WrapperContainer>
  );
};
export default ActualitesPageWrapper;
