import React from "react";
import Layout from "../../layout/Layout";
import ActualitesPageWrapper from "../wrapper/actualites-wrapper/ActualitesPageWrapper";

const Index = ({ pageContext }) => {
  return (
    <Layout>
     
      <ActualitesPageWrapper content={pageContext} />
    </Layout>
  );
};

export default Index;

export const Head = () => <title>Nos -actualités </title>;
