import { GatsbyImage, getImage } from "gatsby-plugin-image";
import React from "react";
import "./action-box-style.scss";
import { Link } from "gatsby";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlay } from "@fortawesome/free-solid-svg-icons";

const ActionBox = ({ Image, title, description, id, href }) => {
  return (
    <div className="action-box">
      <div className="action-box__content">
        <div className="action-box__image">
          <GatsbyImage
            className="action-box__image-block"
            image={getImage(Image)}
          />
        </div>
        <div className="action-box__info">
          <h3 className="action-box__info-title">{title}</h3>
          <div className="action-box__info-description">{description}</div>

          <Link to={`${href}${id}`} className="action-box__info-btn-action">
            <FontAwesomeIcon icon={faPlay} />
            lire
          </Link>
        </div>
      </div>
    </div>
  );
};

export default ActionBox;
